export default {
  layout: "Dum-content",
  mounted() {
    this.getCommittee();

  },
  data() {
    return {
      committees: [],
      errors: {},
      base_url:process.env.url
    };
  },
  methods: {
    getCommittee() {
      this.$axios
        .$get("/committee/show")
        .then((res) => {
          this.committees = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },    
    committeeDelete(id) {        
      if (confirm("Are you sure to delete this committee?")) {
        this.$axios
          .$get("/committee/delete/" + id)
          .then((res) => {
            this.getCommittee();
            this.$toaster.error(res.message);
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    committeeStatus(id) {
      this.$axios
        .$get("/committee/status/" + id)
        .then((res) => {
          this.getCommittee();
          this.$toaster.success(res.message);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
